@charset "UTF-8";
li.filterList.nav-item {
  width: 8rem;
}

ul.Btn-list {
  display: flex;
}

a.filter-btn {
  padding: 15px;
  color: #777;
}

#btnList li a.active {
  background-color: #252362 !important;
  color: #fff !important;
}

.get-a-quote-container.row {
  background: #fff;
  border: 1px solid #ccc;
  border-radius: 5px;
}

#btnList li a:hover {
  cursor: pointer;
  background-color: #252362 !important;
  color: #fff !important;
}

.tableDesign {
  padding: 19px;
  margin-bottom: 20px;
  background-color: #fff;
  border: 1px solid #d4d4d4;
  border-radius: 4px;
  -webkit-box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.05);
  box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.05);
}

.subheading {
  background-color: #252362 !important;
  padding: 5px 12px;
}
.subheading h3 {
  color: #fff !important;
}

.custombtn {
  background: #252362;
  color: #fff;
  border: 2px solid #252362;
  border-radius: 30px;
  min-width: 200px;
  padding: 7px 20px;
  box-shadow: none;
  text-decoration: none;
  font-size: 16px;
}

.custombtn:hover {
  background: #fff;
  color: #252362;
  box-shadow: none;
  text-decoration: none;
}

.form-control-static {
  min-height: 34px;
  padding-top: 7px;
  padding-bottom: 7px;
  margin-bottom: 0;
}

label {
  display: inline-block;
  max-width: 100%;
  margin-bottom: 5px;
  font-weight: 700;
}

#quote-section-1 h2 {
  font-weight: 500;
}

#quote-section-1 h3 {
  font-weight: 600;
  margin-top: 3px;
}

.text-black {
  color: #000;
}

.text-blue {
  color: #252362;
}

.productRow {
  border: 1px solid #d4d4d4;
}

#quote-section-1 .quote-container {
  box-shadow: 0 0 12px 0 #ddd;
  padding: 50px;
}

h2 {
  font-size: 40px;
}

.form-control {
  border-radius: 50px;
  border: 1px solid #ccc;
  height: 40px;
  font-size: 16px;
  font-weight: 600;
  color: #252362;
  margin: 12px 12px 20px 12px;
  background: #fff;
  padding: 7px 10px;
}

ul {
  margin-top: 0;
  margin-bottom: 10px;
}

ul.center-block li {
  padding: 0 0 0 40px;
}

ul.center-block {
  position: relative;
  display: block;
  margin-right: auto;
  margin-left: auto;
}

::placeholder {
  height: 50px;
  font-size: 16px;
  font-weight: 600;
  color: #252362;
  padding: 10px;
}

.center-block li:before {
  content: "âœ“";
  padding: 1px 8px 1px 15px;
  color: green;
  position: absolute;
  left: 9px;
  font-weight: 900;
}

.para {
  text-align: center;
  margin-top: 20px;
  line-height: 2;
}

.heading {
  font-size: 50px;
  font-weight: 500;
  letter-spacing: 3px;
  margin-top: 100px;
  line-height: 1.5;
}

a:active {
  background-color: red;
}